import React from "react";
import { useDispatch } from "react-redux";
import { setUserData } from "../Slices/userSlice";
import "./Registration.scss";
import { Formik } from "formik";
import * as yup from "yup";
import { Link } from "react-router-dom";

const Registration = React.memo(() => {
  const dispatch = useDispatch();
  let userArr = JSON.parse(localStorage.getItem("userArr"));

  const validationSchema = yup.object().shape({
    //        yup
    email: yup // -        - 
      .string()
      .email(" ")
      .typeError("  ")
      .required(" email"),
    password: yup // -        - 
      .string()
      .typeError("    ")
      .required(" ")
      .min(8, "    8 "),
    subscribed: yup.boolean().oneOf([true], " "),
  });

  return (
    <div className="registration">
      <Formik
        initialValues={{ email: "", password: "", subscribed: false }}
        validateOnBlur //    
        onSubmit={(values) => {
          //       
          if (!userArr)
            dispatch(
              setUserData({
                data: Object.values(values),
              })
            );
          else
            userArr?.forEach((userItem) => {
              if (values.email === userItem.email) {
                const underButtonError = document.querySelector(".login-error");
                underButtonError.style.visibility = "visible";
              }

              if (userArr.length !== 0 && values.email !== userItem.email) {
                dispatch(
                  setUserData({
                    data: Object.values(values),
                  })
                );
              }
            });
        }}
        validationSchema={validationSchema} //    yup
      >
        {({
          //        
          values, // 
          errors, // 
          touched, //   
          handleChange, //      
          handleBlur, //      
          isValid, //        (..  )
          handleSubmit, //       
          dirty, //       -
        }) => (
          <div className="main-wrapper">
            <div className="sign-up-link">
              <Link to="/login"></Link>
            </div>
            <div className="title">
              <span></span>
            </div>
            <div className="form">
              <form action="#">
                <label className="email">Email</label>
                <div className="form__input-wrapper email-wrapper">
                  <span
                    className={
                      touched.email && errors.email
                        ? "reqsymbolError"
                        : "reqsymbol"
                    }
                  >
                    *
                  </span>
                  <input
                    className={touched.email && errors.email ? "error" : null}
                    type="email"
                    name="email"
                    id="emailSignUp"
                    placeholder=" email"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.email}
                  />
                  {touched.email && errors.email ? (
                    <span className="under-input-line">{errors.email}</span>
                  ) : null}
                </div>

                <label className="password"></label>
                <div className="form__input-wrapper password-wrapper">
                  <span
                    className={
                      touched.password && errors.password
                        ? "reqsymbolError"
                        : "reqsymbol"
                    }
                  >
                    *
                  </span>
                  <input
                    className={
                      touched.password && errors.password ? "error" : null
                    }
                    type="password"
                    name="password"
                    id="passwordSignUp"
                    placeholder=" "
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.password}
                  />
                  {touched.password && errors.password ? (
                    <span className="under-input-line">{errors.password}</span>
                  ) : null}
                </div>

                <div className="form__input-wrapper checkbox-wrapper">
                  <span
                    className={
                      touched.subscribed && errors.subscribed
                        ? "reqsymbolError"
                        : "reqsymbol"
                    }
                  >
                    *
                  </span>
                  <div className="form__checkbox">
                    <input
                      className={
                        touched.subscribed && errors.subscribed
                          ? "invisible"
                          : null
                      }
                      onChange={handleChange}
                      checked={values.subscribed}
                      name="subscribed"
                      type="checkbox"
                      id="checkboxSignUp"
                    />
                    <label id="checklabel" htmlFor="checkboxSignUp"></label>
                    <span>     </span>
                  </div>
                  {touched.subscribed && errors.subscribed ? (
                    <span className="under-input-line">
                      {errors.subscribed}
                    </span>
                  ) : null}
                </div>

                <div className="button">
                  <button
                    type="submit"
                    onClick={handleSubmit}
                    disabled={!isValid && !dirty}
                    className="signUp"
                  >
                    
                  </button>
                  <span className="login-error">
                      
                  </span>
                </div>
              </form>
            </div>
          </div>
        )}
      </Formik>
    </div>
  );
});

export default Registration;
