import React from "react";
import "./Login.scss";
import { useDispatch } from "react-redux";
import { setUserLoggedIn } from "../Slices/userSlice";
import { Formik } from "formik";
import * as yup from "yup";
import { Link } from "react-router-dom";

const Login = React.memo(() => {
  const dispatch = useDispatch();
  let userArr = JSON.parse(localStorage.getItem("userArr"));

  const validationSchema = yup.object().shape({
    //        yup
    email: yup // -        - 
      .string()
      .email(" ")
      .typeError("  ")
      .required(" email"),
    password: yup // -        - 
      .string()
      .typeError("    ")
      .required(" ")
      .min(8, "    8 "),
  });

  return (
    <div className="login">
      {/* <!-- Log-in --> */}
      <Formik
        initialValues={{ email: "", password: "", subscribed: false }}
        validateOnBlur //    
        onSubmit={(values) => {
          //       
          userArr?.forEach((user) => {
            if (
              values.email === user.email &&
              +values.password === +user.password
            ) {
              dispatch(
                setUserLoggedIn({
                  email: values.email,
                  password: values.password,
                })
              );

              window.location.reload();
            }
            if (
              values.email !== user.email &&
              +values.password !== +user.password
            ) {
              const underButtonError = document.querySelector(".login-error");
              underButtonError.style.visibility = "visible";
            }
          });
        }}
        validationSchema={validationSchema} //    yup
      >
        {({
          //        
          values, // 
          errors, // 
          touched, //   
          handleChange, //      
          handleBlur, //      
          isValid, //        (..  )
          handleSubmit, //       
          dirty, //       -
        }) => (
          <div className="main-wrapper">
            <div className="sign-up-link">
              <Link to="/signin"></Link>
            </div>
            <div className="title">
              <span></span>
            </div>
            <div className="form">
              <form action="#">
                <label className="email">Email</label>
                <div className="form__input-wrapper email-wrapper">
                  <span
                    className={
                      touched.email && errors.email
                        ? "reqsymbolError"
                        : "reqsymbol"
                    }
                  >
                    *
                  </span>
                  <input
                    className={touched.email && errors.email ? "error" : null}
                    type="email"
                    name="email"
                    id="emailSignUp"
                    placeholder=" email"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.email}
                  />
                  {touched.email && errors.email ? (
                    <span className="under-input-line">{errors.email}</span>
                  ) : null}
                </div>

                <label className="password"></label>
                <div className="form__input-wrapper password-wrapper">
                  <span
                    className={
                      touched.password && errors.password
                        ? "reqsymbolError"
                        : "reqsymbol"
                    }
                  >
                    *
                  </span>
                  <input
                    className={
                      touched.password && errors.password ? "error" : null
                    }
                    type="password"
                    name="password"
                    id="passwordSignUp"
                    placeholder=" "
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.password}
                  />
                  {touched.password && errors.password ? (
                    <span className="under-input-line">{errors.password}</span>
                  ) : null}
                </div>

                <div className="button">
                  <button
                    type="submit"
                    onClick={handleSubmit}
                    disabled={!isValid && !dirty}
                    className="signUp"
                  >
                    
                  </button>
                  <span className="login-error">
                      , email  
                  </span>
                </div>
              </form>
            </div>
          </div>
        )}
      </Formik>
    </div>
  );
});

export default Login;
